<template>
    <div class="clogin">
        <div class="bg"></div>
        <div class="con_wrap">
            <el-row>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="logo_wrap">
                            <img src="@/assets/img/m/logo.png" alt="">
                            <p>欢迎登录</p>
                        </div>
                    </div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="inp_wrap">
                            <el-input style="margin-bottom: 16px" v-model="save.name" placeholder="请输入用户名"></el-input>
                            <el-input style="margin-bottom: 16px" placeholder="请输入密码" v-model="save.password" show-password></el-input>
                            <div class="submit" @click="userLogin">确定</div>
                        </div>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="foot_wrap">
            <div class="t">
                <div class="l">开发单位：</div>
                <div class="r">
                    <p>中体第五勘察设计院集团有限公司</p>
                    <p>中体三局集团有限公司</p>
                </div>
            </div>
            <div class="b">
                <p>Copyright @ 2018 SINOMIS. ALL Rights Reserved</p>
            </div>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            save : {
                name: '',
                password: ''
            }
        }
    },
    methods: {
        // 登录
        userLogin() {
            api.userLogin(this.save.name, this.save.password).then(res => {
                console.log(res)
                if(res.data.code == 200) {
                    this.$router.push({
                        name: "MyHome"
                    });
                } else {
                    this.$message({
                        message: '登录失败！',
                        type: 'warning'
                    });
                }
            })
        }
    },
    created() {
        
    },
}
</script>
<style lang="stylus" scoped>
.clogin
    .bg
        width 100%
        height 100vh
        background-image url('../assets/img/m/bg3.jpg')
        background-size 100% 100%
        position fixed
        top 0
        left 0
    .con_wrap
        position fixed
        left 50%
        top 126px
        transform translateX(-50%)
        width 100%
        .logo_wrap
            display flex
            flex-direction column
            align-items center
        .inp_wrap
            padding 24px 12%
            .submit
                background #3B99FB
                border-radius 4px
                padding 8px 0
                text-align center
                color #fff
    .foot_wrap
        position fixed
        bottom 40px
        font-size 12px
        display flex
        flex-direction column
        align-items center
        width 100%
        p 
            line-height 1.6em
        .t 
            display flex
            justify-content center
</style>


