<template>
    <div class="myClda">
        <div class="mytopBar">
            <p>测量统计</p>
            <img src="@/assets/img/m/out.png" alt="">
        </div>
        <!-- 选择线路 -->
        <div class="select">
            <div class="l">线路</div>
            <div class="r">
                <el-dropdown @command="changeLine" trigger="click">
                    <span class="el-dropdown-link">
                        {{save.lineMsg.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in save.lineArr" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            lineID: '',
            save : {
                lineArr: [],
                lineMsg: {},
            }
        }
    },
    methods: {
        // 改变线路
        changeLine(val) {
            console.log(val)
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log('获取线路', res.data.list)
                this.save.lineArr = res.data.list;
                this.save.lineMsg = res.data.list[0];
                this.lineID = res.data.list[0].id;
            })
        },
    },
    watch: {
        lineID(val) {
            console.log('lineID', val);
        }
    },
    created() {
        this.getSelectAll();
    },
}
</script>
<style lang="stylus" scoped>
.myClda
    padding-top 40px
    width 100%
    height 100%
    box-sizing border-box
    padding-bottom 200px
    .mytopBar
        position fixed
        width 100%
        top 0
        height 40px
        background #fff
        z-index 10
        p
            position absolute
            top 50%
            transform translateY(-50%)
            width 100%
            text-align center
        img 
            position absolute
            top 50%
            transform translateY(-50%)
            right 12px
            width 24px
    .select
        background #FAFAFA
        display flex
        align-items center
        justify-content space-between
        padding 12px
        border-bottom 1px solid #eee
        font-size 14px
    .jieguo
        .title 
            display flex
            align-items center
            justify-content space-between
            font-size 14px
            padding 12px
            border-bottom 1px solid #eee
            .more 
                display flex
                align-items center
                i 
                    font-size 16px
        .tableWrap
            padding 12px



</style>


