import axios from 'axios'

// 全局的 axios 默认值
// axios.defaults.baseURL = 'https://app.rencaijob.com';
// axios.defaults.baseURL = 'http://localhost:8089';
// axios.defaults.baseURL = '127.0.0.1:8080/railwayTurnout';
// axios.defaults.baseURL = 'http://127.0.0.1:8080/railwayTurnout';
// axios.defaults.baseURL = 'http://127.0.0.1:8080/railwayTurnout';
// axios.defaults.baseURL = 'http://railwayTurnout';
// axios.defaults.baseURL = 'http://192.168.1.161:8089';  // 线下
axios.defaults.baseURL = '/railwayTurnout';  // 线上





// 添加请求拦截器
axios.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    config.params = {
        ...config.params,
        // khdbs: 2
    }
    return config;
}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});


export default axios;




