<template>
    <div class="myHome">
        <div class="mytopBar">
            <p>首页</p>
            <img @click="out" src="@/assets/img/m/out.png" alt="">
        </div>
        <!-- 选择线路 -->
        <div class="select">
            <div class="l">线路</div>
            <div class="r">
                <el-dropdown  @command="changeLine" trigger="click">
                    <span class="el-dropdown-link">
                        {{save.lineMsg.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in save.lineArr" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
        <!-- 选择站点 -->
        <div class="select">
            <div class="l">站点</div>
            <div class="r">
                <el-dropdown @command="changeSite" trigger="click">
                    <span class="el-dropdown-link">
                        {{save.siteMsg.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in save.siteArr" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
        <!-- 检测项目 -->
        <div class="jieguo">
            <div class="title">
                <p>实测数据</p>
                <!-- <p class="more">更多 <i class="el-icon-arrow-right"></i></p> -->
            </div>
            <div class="tableWrap">
                    <template>
                        <el-table :data="save.projectArr" style="width: 100%">
                        <el-table-column prop="pname" label="项目名称"></el-table-column>
                        <el-table-column prop="ptime" label="检测时间"></el-table-column>
                        <el-table-column prop="warningstatistics" label="预警统计"></el-table-column>
                    </el-table>
                </template>
            </div>
        </div>
        <!-- 小车数据 -->
        <div class="jieguo">
            <div class="title">
                <p>小车数据</p>
                <!-- <p class="more">更多 <i class="el-icon-arrow-right"></i></p> -->
            </div>
            <div class="tableWrap">
                    <template>
                        <el-table :data="save.smartCarData" style="width: 100%">
                        <el-table-column prop="name" label="参数"></el-table-column>
                        <el-table-column prop="list[0].num" label="实测参数"></el-table-column>
                        <el-table-column prop="mileage.num" label="里程"></el-table-column>
                        <el-table-column prop="list[0].sitename" label="统计分析"></el-table-column>
                    </el-table>
                </template>
            </div>
        </div>
        <!-- 天气 -->
        <div class="weather">
            <p>温度：{{save.weather.temp}}</p>
            <p>湿度：{{save.weather.SD}}</p>
            <p>气压：{{save.weather.AP}}</p>
            <p>{{save.weather.WD}}：{{save.weather.WS}}</p>
            <p>城市：{{save.weather.city}}</p>
        </div>
        <!-- 项目信息 -->
        <div class="project">
            <p>项目概况：{{save.projectMsg.projectoverview}}</p>
            <p>业主通知：{{save.projectMsg.noticeowner}}</p>
            <p>地理位置：{{save.projectMsg.geographical}}</p>
            <p>施工进度：{{save.projectMsg.progressconstruction}}</p>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            lineID: '',
            siteID: '',
            weatherid: '',
            save : {
                lineArr: [],
                lineMsg: {},
                siteArr: [],
                siteMsg: {},
                smartCarData: [],
                projectArr: [],
                projectMsg: {},
                weather: {}
            }
        }
    },
    methods: {
        out() {
            if(confirm('是否退出')) {
                console.log('退出')
                this.$router.push({
                    name: "mLogin"
                });
            } else {
                console.log('取消')
            }
        },
        // 改变站点
        changeSite(val) {
            console.log('改变站点', val);
            this.save.siteMsg = val;
            this.siteID = val.id;
        },
        // 改变线路
        changeLine(val) {
            console.log('改变线路', val);
            this.save.lineMsg = val;
            this.lineID = val.id;
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log('获取线路', res.data.list)
                this.save.lineArr = res.data.list;
                this.save.lineMsg = res.data.list[0];
                this.lineID = res.data.list[0].id;
            })
        },
        // 获取站点
        getLinesite(id) {
            api.getLinesite(id).then(res => {
                console.log('获取站点', res.data.list);
                this.save.siteArr = res.data.list;
                this.save.siteMsg = res.data.list[0];
                this.siteID = res.data.list[0].id;
            })
        },
        // 获取小车数据
        getSmartCarData() {
            api.getSmartCarData().then(res => {
                console.log('获取小车数据', res.data.msg);
                this.save.smartCarData = res.data.msg;
            })
        },
        // 获取首页项目数据
        getMeasurementproject(id, num) {
            api.getMeasurementproject(id, num, 10).then(res => {
                console.log('获取首页项目数据', res.data.list);
                this.save.projectArr = res.data.list;
            })
        },
        // 获取项目信息
        getMainpageinformactionSelectbyrid(id) {
            api.getMainpageinformactionSelectbyrid(id).then(res => {
                console.log('获取项目信息', res.data.msg);
                this.weatherid = res.data.msg.weatherid;
                this.save.projectMsg = res.data.msg;
            })
        },
        // 获取天气
        getWeather(id) {
            api.getWeather(id).then(res => {
                var weather = JSON.parse(res.data.result);
                console.log('获取天气', weather.weatherinfo);
                this.save.weather = weather.weatherinfo;
            })
        }
    },
    watch: {
        lineID(val) {
            console.log('lineID', val);
            this.getLinesite(val);
        },
        siteID(val) {
            console.log('siteID', val);
            this.getMeasurementproject(val, 0);
            this.getMainpageinformactionSelectbyrid(val);
        },
        weatherid(val) {
            this.getWeather(val);
        }
    },
    created() {
        this.getSelectAll();
        this.getSmartCarData();
    },
}
</script>
<style lang="stylus" scoped>
.myHome
    padding-top 40px
    width 100%
    height 100%
    box-sizing border-box
    padding-bottom 200px
    .mytopBar
        position fixed
        width 100%
        top 0
        height 40px
        background #fff
        z-index 10
        p
            position absolute
            top 50%
            transform translateY(-50%)
            width 100%
            text-align center
        img 
            position absolute
            top 50%
            transform translateY(-50%)
            right 12px
            width 24px
            z-index 11
    .select
        background #FAFAFA
        display flex
        align-items center
        justify-content space-between
        padding 12px
        border-bottom 1px solid #eee
        font-size 14px
    .jieguo
        margin 12px
        box-shadow 2px 2px 20px rgba(0,0,0,0.05), -1px -1px 15px rgba(0,0,0,0.01)
        border-radius 4px
        .title 
            display flex
            align-items center
            justify-content space-between
            font-size 14px
            padding 12px
            border-bottom 1px solid #eee
            .more 
                display flex
                align-items center
                i 
                    font-size 16px
        .tableWrap
            padding 12px
    .weather
        margin 12px
        box-shadow 2px 2px 20px rgba(0,0,0,0.05), -1px -1px 15px rgba(0,0,0,0.01)
        border-radius 4px
        padding 12px
    .project
        margin 12px
        box-shadow 2px 2px 20px rgba(0,0,0,0.05), -1px -1px 15px rgba(0,0,0,0.01)
        border-radius 4px
        padding 12px





</style>


