import Vue from 'vue'
import Router from 'vue-router'
import MyLogin from '@/components/login'
import MyHome from '@/components/home'
import MyClda from '@/components/clda'
import MyClbz from '@/components/clbz'
import MyCltj from '@/components/cltj'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'mLogin',
      component: MyLogin
    },
    {
      path: '/MyHome',
      name: 'MyHome',
      component: MyHome
    },
    {
      path: '/MyLogin',
      name: 'mLogin',
      component: MyLogin
    },
    {
      path: '/MyClda',
      name: 'MyClda',
      component: MyClda
    },
    {
      path: '/MyClbz',
      name: 'MyClbz',
      component: MyClbz
    },
    {
      path: '/MyCltj',
      name: 'MyCltj',
      component: MyCltj
    },
  ]
})



