<template>
    <div class="myClda">
        <div class="mytopBar">
            <p>测量档案</p>
            <img @click="out" src="@/assets/img/m/out.png" alt="">
        </div>
        <!-- 选择线路 -->
        <div class="select">
            <div class="l">线路</div>
            <div class="r">
                <el-dropdown @command="changeLine" trigger="click">
                    <span class="el-dropdown-link">
                        {{save.lineMsg.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in save.lineArr" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
        <!-- 选择站点 -->
        <div class="select">
            <div class="l">站点</div>
            <div class="r">
                <el-dropdown @command="changeSite" trigger="click">
                    <span class="el-dropdown-link">
                        {{save.siteMsg.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in save.siteArr" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
        <!-- 选择项目 -->
        <div class="select">
            <div class="l">项目</div>
            <div class="r">
                <el-dropdown @command="changeProject" trigger="click">
                    <span class="el-dropdown-link">
                        {{save.projectMsg.pname}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in save.projectArr" :key="item.id" :command="item">{{item.pname}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
        <!-- 检测结果 -->
        <div class="jieguo">
            <div class="title">
                <p>检测结果</p>
            </div>
            <div class="tableWrap">
                    <template>
                        <el-table :data="save.jieguoArr" style="width: 100%">
                        <el-table-column prop="pname" label="项目名称"></el-table-column>
                        <el-table-column prop="ptime" label="检测时间"></el-table-column>
                        <el-table-column prop="warningstatistics" label="预警统计"></el-table-column>
                    </el-table>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            lineID: '',
            siteID: '',
            projectID: '',
            save : {
                lineArr: [],
                lineMsg: {},
                siteArr: [],
                siteMsg: {},
                projectArr: [],
                projectMsg: {},
                jieguoArr: [],
            }
        }
    },
    methods: {
        // 退出
        out() {
            if(confirm('是否退出')) {
                console.log('退出')
                this.$router.push({
                    name: "mLogin"
                });
            } else {
                console.log('取消')
            }
        },
        // 改变项目
        changeProject(val) {
            console.log('改变项目', val);
            this.save.projectMsg = val;
            this.projectID = val.id;
            this.getMeasurementprojectSelectBy('', val.pname, 0)
        },
        // 改变线路
        changeLine(val) {
            console.log('改变线路', val);
            this.save.lineMsg = val;
            this.lineID = val.id;
        },
        // 改变站点
        changeSite(val) {
            console.log('改变站点', val);
            this.save.siteMsg = val;
            this.siteID = val.id;
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log('获取线路', res.data.list)
                this.save.lineArr = res.data.list;
                this.save.lineMsg = res.data.list[0];
                this.lineID = res.data.list[0].id;
            })
        },
        // 获取站点
        getLinesite(id) {
            api.getLinesite(id).then(res => {
                console.log('获取站点', res.data.list);
                this.save.siteArr = res.data.list;
                this.save.siteMsg = res.data.list[0];
                this.siteID = res.data.list[0].id;
            })
        },
        // 项目下拉
        getMeasurementprojectSelectSelectBymid(id, page) {
            api.getMeasurementprojectSelectSelectBymid(id, page, 10).then(res => {
                console.log('项目下拉', res.data.list);
                this.save.projectArr = res.data.list;
                this.save.projectMsg = res.data.list[0];
                this.projectID = res.data.list[0].id;
            })
        },
        // 获取测量档案
        getMeasurementprojectSelectBy(id, mName, pageNum) {
            api.getMeasurementprojectSelectBy(id, mName, pageNum).then(res => {
                console.log('获取测量档案', res.data.list);
                this.save.jieguoArr = res.data.list;
            })
        }
    },
    watch: {
        lineID(val) {
            console.log('lineID', val);
            this.getLinesite(val);
        },
        siteID(val) {
            console.log('siteID', val);
            this.getMeasurementprojectSelectSelectBymid(val, 0);
            this.getMeasurementprojectSelectBy(val, '', 0);
        },
        projectID(val) {
            console.log('projectID', val);
        }
    },
    created() {
        this.getSelectAll();
    },
}
</script>
<style lang="stylus" scoped>
.myClda
    padding-top 40px
    width 100%
    height 100%
    box-sizing border-box
    padding-bottom 200px
    .mytopBar
        position fixed
        width 100%
        top 0
        height 40px
        background #fff
        z-index 10
        p
            position absolute
            top 50%
            transform translateY(-50%)
            width 100%
            text-align center
        img 
            position absolute
            top 50%
            transform translateY(-50%)
            right 12px
            width 24px
    .select
        background #FAFAFA
        display flex
        align-items center
        justify-content space-between
        padding 12px
        border-bottom 1px solid #eee
        font-size 14px
    .jieguo
        .title 
            display flex
            align-items center
            justify-content space-between
            font-size 14px
            padding 12px
            border-bottom 1px solid #eee
            .more 
                display flex
                align-items center
                i 
                    font-size 16px
        .tableWrap
            padding 12px



</style>


