<template>
  <div id="app">
    <router-view/>
    <tab-bar class="tabBar" v-show="tabBarShow"></tab-bar>
  </div>
</template>

<script>
import tabBar from '@/components/tabBar.vue';
export default {
  name: 'App',
  components: {
    tabBar
  },
  data () {
    return {
      tabBarShow: true
    }
  },
  watch: {
        $route(to){
            console.log(to.path);
            // this.active = to.path.replace(/\//, '');
            if(to.path == '/') {
              this.tabBarShow = false;
            } else {
              this.tabBarShow = true;
            }
        }
  },
  created() {
      console.log(this.$route.path)
      if(this.$route.path == '/') {
        this.tabBarShow = false
      } else {
        this.tabBarShow = true;
      }
  },
}
</script>
<style lang="stylus">
* 
  margin 0
  padding 0
  .tabBar
    position fixed
    bottom 0
    width 100%
    z-index 1
</style>


