<template>
    <div class="myClbz">
        <div class="mytopBar">
            <img class="l" @click="myAdd" src="@/assets/img/m/add.png" alt="">
            <p>测量标准</p>
            <img @click="out" class="r" src="@/assets/img/m/out.png" alt="">
        </div>
        <!-- 检测结果 -->
        <div class="jieguo">
            <div class="title">
                <p>检测标准</p>
                <!-- <p class="more">更多 <i class="el-icon-arrow-right"></i></p> -->
            </div>
            <div class="tableWrap">
                    <template>
                        <el-table :data="save.tableData" style="width: 100%">
                        <el-table-column prop="msg1" label="标准"></el-table-column>
                        <el-table-column prop="msg" label="说明"></el-table-column>
                        <el-table-column prop="warningstatistics" label="操作">
                            <template slot-scope="scope">
                                <el-button @click="myEdit(scope.row)" type="primary" size="mini" icon="el-icon-edit" circle></el-button>
                                <el-button @click="myDel(scope.row)" type="danger" size="mini" icon="el-icon-delete" circle></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </div>

        <!-- 删除 -->
        <div class="alert_bg" v-show="status"></div>
        <div class="alert_del" v-show="status == 'del'">
            <div class="tips">提示</div>
            <div class="t"> <i class="el-icon-warning"></i> 此操作将永久删除该标准, 是否继续?</div>
            <div class="b">
                <el-button size='mini' @click="close">取消</el-button>
                <el-button size="mini" @click="delSubmit" type="primary">确定</el-button>
            </div>
        </div>
        <!-- 修改 新增 -->
        <div class="alert_con" v-show="status == 'new' || status == 'edit'">
            <div class="title">{{status == 'new' ? '新增' : '修改'}}</div>
            <div class="l">
                <p>编号：</p>
                <el-input v-model="save.msg.num" placeholder="请输入内容"></el-input>
            </div>
            <div class="l">
                <p>标准：</p>
                <el-input v-model="save.msg.msg1" placeholder="请输入内容"></el-input>
            </div>
            <div class="l">
                <p>说明：</p>
                <el-input type="textarea" autosize placeholder="请输入内容" v-model="save.msg.msg"></el-input>
            </div>
            <div class="l">
                <p>分类：</p>
                <div>
                    <template>
                        <el-radio v-model="save.msg.identifiernum" label="1">备选项</el-radio>
                        <el-radio v-model="save.msg.identifiernum" label="2">备选项</el-radio>
                    </template>
                </div>
            </div>
            <div class="b">
                <el-button size='mini' @click="close">取消</el-button>
                <el-button size="mini" @click="submit" type="primary">确定</el-button>
            </div>
        </div>
        <!-- 提示 -->
        <div class="alert_tips" v-show="thipsShow">
            <p>{{tips}}</p>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            tips: '',
            status: false, // del new edit tips
            thipsShow: false,
            save : {
                delD: '',
                tableData: [],
                msg: {}
            }
        }
    },
    methods: {
        // 退出
        out() {
            if(confirm('是否退出')) {
                console.log('退出')
                this.$router.push({
                    name: "mLogin"
                });
            } else {
                console.log('取消')
            }
        },
        // 新增
        myAdd() {
            this.status = 'new';
            this.save.msg = {
                identifiernum: '1',
                msg: '',
                msg1: '',
                num: ''
            }
        },
        // 新增 修改
        submit() {
            switch(this.status) {
                case 'new': // 新增
                    this.newQualitystandard(this.save.msg);
                    break;
                case 'edit': // 修改
                    this.changeQualitystandard(this.save.msg);
                    break;
            }
        },
        // 删除
        delSubmit() {

        },
        // 取消
        close() {
            this.status = false;
        },
        // 删除
        myDel(val) {
            console.log('删除', val);
            this.save.delD = val.id;
            this.status = 'del'
        },
        // 修改
        myEdit(val) {
            console.log('修改', val);
            this.status = 'edit';
            this.save.msg = val;
        },
        // 获取 质量分析标准
        geTqualitystandard() {
            api.geTqualitystandard().then(res => {
                console.log('获取 质量分析标准', res.data.result);
                this.save.tableData = res.data.result;
            })
        },
        // 修改上传
        changeQualitystandard(save) {
            api.changeQualitystandard(save.id, {
                identifiernum: save.identifiernum,
                msg: save.msg,
                msg1: save.msg1,
                num: save.num
            }).then(res => {
                console.log(res);
                this.status = false;
                if(res.data.status == 'ok') {
                    this.myTips('修改成功！', 1500);
                    this.geTqualitystandard();
                } else {
                    this.myTips('修改失败！', 1500);
                }
            })
        },
        // 新增
        newQualitystandard(save) {
            api.newQualitystandard({
                identifiernum: save.identifiernum,
                msg: save.msg,
                msg1: save.msg1,
                num: save.num
            }).then(res => {
                console.log(res);
                this.status = false;
                if(res.data.status == 'ok') {
                    this.myTips('新增成功！', 1500);
                    this.geTqualitystandard();
                } else {
                    this.myTips('新增失败！', 1500);
                }
            })
        },
        // 提示函数
        myTips(tips, time) {
            this.tips = tips;
            this.thipsShow = true;
            setTimeout(() => {
                this.thipsShow = false;
            }, time)
        }
    },
    watch: {
        
    },
    created() {
        this.geTqualitystandard();
    },
}
</script>
<style lang="stylus" scoped>
.myClbz
    padding-top 40px
    width 100%
    height 100%
    box-sizing border-box
    padding-bottom 200px
    .mytopBar
        position fixed
        width 100%
        top 0
        height 40px
        background #fff
        z-index 10
        p
            position absolute
            top 50%
            transform translateY(-50%)
            width 100%
            text-align center
        img 
            position absolute
            top 50%
            transform translateY(-50%)
            right 12px
            width 24px
            z-index 11
        .l 
            left 12px
    .select
        background #FAFAFA
        display flex
        align-items center
        justify-content space-between
        padding 12px
        border-bottom 1px solid #eee
        font-size 14px
    .jieguo
        margin 12px
        box-shadow 2px 2px 20px rgba(0,0,0,0.05), -1px -1px 15px rgba(0,0,0,0.01)
        border-radius 4px
        .title 
            display flex
            align-items center
            justify-content space-between
            font-size 14px
            padding 12px
            border-bottom 1px solid #eee
            .more 
                display flex
                align-items center
                i 
                    font-size 16px
        .tableWrap
            padding 12px
    .alert_bg
        width 100%
        height 100vh
        background rgba(0,0,0,.5)
        position fixed
        left 0
        top 0
        z-index 900
    .alert_del
        width 90%
        background #fff
        border-radius 4px
        position fixed
        left 50%
        top 50%
        transform translate(-50%, -50%)
        z-index 999
        padding 12px
        color #666
        box-sizing border-box
        .tips
            font-size 18px
            padding-bottom 12px
        .t 
            display flex
            align-items center
            i 
                color #E6A23C
                font-size 22px
                margin-right 6px
        .b
            padding-top 12px
            display flex
            justify-content flex-end
    .alert_con
        width 90%
        background #ffffff
        position fixed
        left 50%
        top 50%
        transform translate(-50%, -50%)
        z-index 999
        padding 16px
        border-radius 4px
        box-sizing border-box
        .title 
            padding-bottom 12px
            font-size 18px
        .l 
            display flex
            align-items center
            padding 6px 0
            p 
                white-space nowrap
        .b
            padding-top 12px
            display flex
            justify-content flex-end
    .alert_tips
        position fixed
        left 50%
        transform translateX(-50%)
        top 100px
        background #fff
        z-index 999
        box-shadow 20px 20px 50px rgba(0,0,0,.1), -20px -20px 50px rgba(0,0,0,.1)
        width 80%
        text-align center
        padding 20px 0
        border-radius 4px






</style>




