<template>
    <div class="myNav">
        <el-row>
            <el-col :span="8">
                <div class="grid-content bg-purple">
                    <router-link to="/MyHome">
                        <img class="t" src="@/assets/img/m/nav1.png" alt="">
                        <img class="b" src="@/assets/img/m/nav11.png" alt="">
                        <p>首页</p>
                    </router-link>
                </div>
            </el-col>
            <el-col :span="8">
                <div class="grid-content bg-purple-light">
                    <router-link to="/MyClda">
                        <img class="t" src="@/assets/img/m/nav2.png" alt="">
                        <img class="b" src="@/assets/img/m/nav21.png" alt="">
                        <p>测量档案</p>
                    </router-link>
                </div>
            </el-col>
            <el-col :span="8">
                <div class="grid-content bg-purple">
                    <router-link to="/MyClbz">
                        <img class="t" src="@/assets/img/m/nav3.png" alt="">
                        <img class="b" src="@/assets/img/m/nav31.png" alt="">
                        <p>测量标准</p>
                    </router-link>
                </div>
            </el-col>
            <!-- <el-col :span="6">
                <div class="grid-content bg-purple-light">
                    <router-link to="/MyCltj">
                        <img src="@/assets/img/m/nav4.png" alt="">
                        <p>测量统计</p>
                    </router-link>
                </div>
            </el-col> -->
        </el-row>
    </div>
</template>
<script>
export default {
    data () {
        return {
            myRouter : '',
            imgUrl: ['@/assets/img/m/nav1.png','@/assets/img/m/nav2.png','@/assets/img/m/nav3.png'],
            imgActiveUrl: ['@/assets/img/m/nav11.png','@/assets/img/m/nav22.png','@/assets/img/m/nav33.png'],
        }
    },
    watch: {
            $route(to){
                console.log('********************RUTER********************', to.path);
                this.myRouter = to.path;
            }
    },
    created() {
        console.log(this.$route.path);
        this.myRouter = this.$route.path;
    },
}
</script>
<style lang="stylus" scoped>
.myNav
    border-top 1px solid #eee
    background #fff
    a
        color #999
        text-decoration none
        display flex
        flex-direction column
        align-items center
        padding 6px 0
        img 
            width 24px
        p
            font-size 12px
            padding-top 1px
        .t 
            display block
        .b 
            display none
    .active 
        color #3B99FB
        .t
            display none
        .b 
            display block
</style>


